.link_button {
    position: relative;
    display: inline-block;
    padding: 12px 30px;
    border: 2px solid #fefefe;
    color: #fefefe;
    text-decoration: none;
    font-size: 2em;
    border-radius: 0.5rem;
    cursor: pointer;
  }
  
  .link_button::before {
    content: '';
    position: absolute;
    top: 6px;
    left: -2px;
    width: calc(100% + 4px);
    height: calc(100% - 12px);
    background-color: #000;
    transition: 0.3s ease-in-out;
    transform: scaleY(1);
  }
  
  .link_button:hover::before {
    transform: scaleY(0);
  }
  
  .link_button::after {
    content: '';
    position: absolute;
    left: 6px;
    top: -2px;
    height: calc(100% + 4px);
    width: calc(100% - 12px);
    background-color: #000;
    transition: 0.3s ease-in-out;
    transform: scaleX(1);
    transition-delay: 0.2s;
  }
  
  .link_button:hover::after {
    transform: scaleX(0);
  }
  
  .link_button .span_button {
    position: relative;
    z-index: 3;
  }
  
  .button_contact {
    text-decoration: none;
    background-color: #000;
    border: none;
    border-radius: 0.5rem;

  }

  @media (max-width: 780px){
    .link_button{
      font-size: 1.5em;
      padding: 10px 30px;

    }
  }
  @media (max-width: 500px){
    .link_button{
      font-size: 1.1em;
      padding: 10px 30px;

    }
  }